'use strict';

/**
* Invoke a callback function once for each ndarray element.
*
* @module @stdlib/ndarray-base-for-each
*
* @example
* var Float64Array = require( '@stdlib/array-float64' );
* var naryFunction = require( '@stdlib/utils-nary-function' );
* var log = require( '@stdlib/console-log' );
* var forEach = require( '@stdlib/ndarray-base-for-each' );
*
* // Create a data buffer:
* var xbuf = new Float64Array( 12 );
*
* // Define the shape of the array:
* var shape = [ 3, 1, 2 ];
*
* // Define the array strides:
* var sx = [ 4, 4, 1 ];
*
* // Define the index offset:
* var ox = 1;
*
* // Create an ndarray-like objects:
* var x = {
*     'ref': null,
*     'dtype': 'float64',
*     'data': xbuf,
*     'shape': shape,
*     'strides': sx,
*     'offset': ox,
*     'order': 'row-major'
* };
*
* // Apply the callback function:
* forEach( [ x ], naryFunction( log, 1 ) );
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
